import { nanoid } from "nanoid";
import React from "react";
import { useRef } from "react";
import { useState } from "react";
import _ from "lodash";

const IELTS = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      reading: "",
      writing: "",
      listing: "",
      speaking: "",
      overall: "",
    },
  ]);
  const inputRefs = useRef([[]]);
  const removesRefs = useRef([]);

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element[0] != null) {
        newData[index].overall = element[0]?.value;
        newData[index].listing = element[1]?.value;
        newData[index].reading = element[2]?.value;
        newData[index].speaking = element[3]?.value;
        newData[index].writing = element[4]?.value;
      }
    });
    return newData;
  };

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };
  const rows = [];
  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()} className="peer-hover:border-red-700">
        <td className="table-cell">
          <input
            max={9}
            min={0}
            step={0.5}
            type="number"
            defaultValue={element.overall}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            className="cell "
          ></input>
        </td>
        <td className="table-cell">
          <input
            max={9}
            min={0}
            step={0.5}
            type="number"
            defaultValue={element.listing}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell "
          ></input>
        </td>
        <td className="table-cell">
          <input
            max={9}
            min={0}
            step={0.5}
            type="number"
            defaultValue={element.reading}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][2] = el;
            }}
            className="cell "
          ></input>
        </td>
        <td className="table-cell">
          <input
            max={9}
            min={0}
            step={0.5}
            type="number"
            defaultValue={element.speaking}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][3] = el;
            }}
            className="cell "
          ></input>
        </td>
        <td className="table-cell">
          <input
            max={9}
            min={0}
            step={0.5}
            type="number"
            defaultValue={element.writing}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][4] = el;
            }}
            className="cell "
          ></input>
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group peer"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-10 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-10 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <th className="table-cell w-44">Overall</th>
            <th className="table-cell w-44">Listing</th>
            <th className="table-cell w-44">Reading</th>
            <th className="table-cell w-44">Speaking</th>
            <th className="table-cell w-44">Writing</th>
          </tr>
          {rows}
        </tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="5"
              onClick={() => {
                const newData = save();
                newData.push({
                  reading: "",
                  writing: "",
                  listing: "",
                  speaking: "",
                  overall: "",
                });
                setData(newData);
              }}
            >
              Add new
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("IELTS", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default IELTS;
