import React, { useState } from "react";
import Degree from "../../components/Degree";
import Universities from "../../components/Universities";

const Twelfth = ({ initialData, setProfile }) => {
  const [data, setData] = useState(initialData);
  const getData = (type, value) => {
    const newData = { ...data, [type]: value };
    setData(newData);
    setProfile("grade12", newData);
  };
  return (
    <div className="space-y-10">
      <div>
        <p>1.Planning for your degree</p>
        <p>
          It is never too early to start thinking about what you will study.
          Knowing which degrees you are interested in applying to will help you
          choose Grade 11 (junior year) and Grade 12 (senior year) courses
          accordingly. Here is a worksheet to help you plan ahead.
        </p>
      </div>
      <Degree setParentData={getData} />
      <div>
        <p>2.University of my dream</p>
        <p>
          Review your requirements Requirements for admission depend on where
          you attended high school, which country/university you hope to attend,
          and which degree are you applying for. To see which requirements are
          relevant to you, visit the official site of university.
        </p>
      </div>
      <Universities setParentData={getData} />
    </div>
  );
};

export default Twelfth;
