import React from "react";
import { useRef } from "react";
import { useState } from "react";

const SWOT = ({ initialData, setParentData, dataName }) => {
  const [data, setData] = useState({
    title: "",
    strength: "",
    weakness: "",
    opportunities: "",
    threats: "",
  });
  const title = useRef();
  const s = useRef();
  const w = useRef();
  const o = useRef();
  const t = useRef();
  return (
    <div className="field">
      <div className="space-x-5">
        <input
          placeholder="Career"
          ref={title}
          className="cell w-96 h-6 bg-gray-200"
        />
      </div>
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr className="">
            <td className="table-cell w-96 p-0">
              <textarea
                ref={s}
                className="cell"
                placeholder="Strength"
              ></textarea>
            </td>
            <td className="table-cell w-96 p-0">
              <textarea
                ref={w}
                className="cell"
                placeholder="Weakness"
              ></textarea>
            </td>
          </tr>
          <tr className="">
            <td className="table-cell w-96 p-0">
              <textarea
                ref={o}
                className="cell"
                placeholder="Opportunities"
              ></textarea>
            </td>
            <td className="table-cell w-96 p-0">
              <textarea
                ref={t}
                className="cell"
                placeholder="Threats"
              ></textarea>
            </td>
          </tr>
        </tbody>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = {
            title: title.current.value,
            strength: s.current.value,
            weakness: w.current.value,
            opportunities: o.current.value,
            threats: t.current.value,
          };
          setData(newData);
          setParentData(dataName, newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default SWOT;
