import React from "react";
import { useState, useRef } from "react";
import _ from "lodash";
import { nanoid } from "nanoid";

const RawUniversities = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      name: "",
      country: "",
    },
  ]);

  const inputRefs = useRef([[]]);
  const removesRefs = useRef([]);

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element[0] != null) {
        newData[index].name = element[0]?.value;
        newData[index].country = element[1]?.value;
      }
    });
    return newData;
  };

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };

  const rows = [];
  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell p-0">1 </td>
        <td className="table-cell p-0">
          <input
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            defaultValue={element.name}
            className="cell "
          ></input>
        </td>
        <td className="table-cell p-0">
          <input
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            defaultValue={element.country}
            className="cell "
          ></input>
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-24 p-3">#</td>
            <td className="table-cell w-96 p-3 ">Name of University</td>
            <td className="table-cell w-96 p-0">Country</td>
          </tr>
          {rows}
        </tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="3"
              onClick={() => {
                const newData = save();
                newData.push({ name: "", country: "" });
                setData(newData);
              }}
            >
              Add new{" "}
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("universities", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default RawUniversities;
