import { nanoid } from "nanoid";
import React from "react";
import { useState, useRef } from "react";
import _ from "lodash";

const IBChoice = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      profession: "",
      description: "",
    },
  ]);

  const inputRefs = useRef([[]]);
  const removesRefs = useRef([]);

  const save = () => {
    const newData = _.cloneDeep(data);
    console.log(inputRefs.current);
    inputRefs.current.forEach((element, index) => {
      if (element[0] != null) {
        newData[index].profession = element[0]?.value;
        newData[index].description = element[1]?.value;
      }
    });
    return newData;
  };

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };

  const rows = [];

  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell ">
          <textarea
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            defaultValue={element.profession}
            className="cell"
          ></textarea>
        </td>
        <td className="table-cell ">
          <textarea
            defaultValue={element.description}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell"
          ></textarea>
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });

  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-96 ">Специальность</td>
            <td className="table-cell w-10 p-3 ">
              Какие DP предметы и оценки нужны для поступления?
            </td>
          </tr>
          {rows}
        </tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="2"
              onClick={() => {
                const newData = save();
                newData.push({ profession: "", description: "" });
                setData(newData);
              }}
            >
              Add new{" "}
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("ibChoice", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default IBChoice;
