import React from "react";
import { useRef } from "react";
import { useState } from "react";

const Degree = ({ initialData, setParentData }) => {
  const [data, setData] = useState({
    admission: {},
    applyDecision: {},
    country: {},
    degreeRequirements: {},
  });
  const t1 = useRef();
  const t2 = useRef();
  const t3 = useRef();
  const t4 = useRef();
  const save = () => {
    const newData = {
      admission: t1.current.value,
      applyDecision: t2.current.value,
      country: t3.current.value,
      degreeRequirements: t4.current.value,
    };
    return newData;
  };
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-[25%] p-3">
              What are your general admission requirements? Review admission
              requirements at official sites of universities(IB, Language)
            </td>
            <td className="table-cell w-[25%] ">
              What are your general admission requirements? Review admission
              requirements at official sites of universities(IB, Language)
            </td>
            <td className="table-cell w-[25%] p-0">
              Which country do you prefer to study in?
            </td>
            <td className="table-cell w-[25%] p-0">
              What are the degree-specific course requirements? Find the
              admission requirements specific to each degree at official sites
              of universities
            </td>
          </tr>
          <tr>
            <td className="table-cell p-0">
              <textarea ref={t1} className="cell "></textarea>
            </td>
            <td className="table-cell p-0">
              <textarea ref={t2} className="cell"></textarea>
            </td>
            <td className="table-cell p-0">
              <textarea ref={t3} className="cell"></textarea>
            </td>
            <td className="table-cell p-0">
              <textarea ref={t4} className="cell"></textarea>
            </td>
          </tr>
        </tbody>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("degreePlan", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default Degree;
