import { nanoid } from "nanoid";
import React, { useRef } from "react";
import { useState } from "react";
import _ from "lodash";

const Goals = ({ initialData, setParentData }) => {
  const [data, setData] = useState([{ name: "" }]);

  const inputRefs = useRef([]);
  const removesRefs = useRef([]);

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };

  const rows = [];

  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell w-16">{index + 1}</td>
        <td className="table-cell w-96">
          <textarea
            defaultValue={data[index].name}
            ref={(el) => (inputRefs.current[index] = el)}
            className="cell"
          ></textarea>
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element !== null) {
        newData[index].name = element.value;
      }
    });
    return newData;
  };

  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <thead>
          <tr>
            <th className="table-cell">#</th>
            <th className="table-cell">Цель</th>
          </tr>
        </thead>
        <tbody>{rows}</tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="2"
              onClick={() => {
                const newData = save();
                newData.push({ name: "" });
                setData(newData);
              }}
            >
              Add new{" "}
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("goals", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default Goals;
