import React from "react";
import { useState } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import Eighth from "./grades/Eighth";
import Eleventh from "./grades/Eleventh";
import Ninth from "./grades/Ninth";
import Seventh from "./grades/Seventh";
import Tenth from "./grades/Tenth";
import Twelfth from "./grades/Twelfth";
import Hobbies from "../components/Hobbies";
import Goals from "../components/Goals";
import SchoolActivity from "../components/SchoolActivity";

const Profile = ({ user }) => {
  const [data, setData] = useState({
    overall: {
      hobbies: {},
      schoolActivity: {},
      goals: {},
    },
    grade7: {
      profession1: {},
      profession2: {},
      universities: {},
    },
    grade8: {
      profession1: {},
      profession2: {},
      universities: {},
    },
    grade9: {
      profession1: {},
      profession2: {},
      universities: {},
    },
    grade10: {
      profession1: {},
      profession2: {},
      universities: {},
      honors: {},
      ibChoice: {},
    },
    grade11: {
      degreePlan: {},
      universities: {},
      honors: {},
      IELTS: {},
      SAT: {},
    },
    grade12: {
      degreePlan: {},
      universities: {},
    },
  });
  const setProfile = (type, value) => {
    const newData = { ...data, [type]: value };
    setData(newData);
  };
  return (
    <div className="w-[60vw] mx-auto">
      <Tabs className="text-center">
        <TabList className="inline-flex space-x-4 mb-10">
          <Tab className="tab-item">Profile</Tab>
          <Tab className="tab-item">Grade 7</Tab>
          <Tab className="tab-item">Grade 8</Tab>
          <Tab className="tab-item">Grade 9</Tab>
          <Tab className="tab-item">Grade 10</Tab>
          <Tab className="tab-item">Grade 11</Tab>
          <Tab className="tab-item">Grade 12</Tab>
        </TabList>
        <TabPanel>
          <ProfileTab initialData={data.overall} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Seventh initialData={data.grade7} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Eighth initialData={data.grade8} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Ninth initialData={data.grade8} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Tenth initialData={data.grade10} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Eleventh initialData={data.grade11} setProfile={setProfile} />
        </TabPanel>
        <TabPanel>
          <Twelfth initialData={data.grade12} setProfile={setProfile} />
        </TabPanel>
      </Tabs>
      <div className="h-16"></div>
    </div>
  );
};

const ProfileTab = ({ initialData, setProfile }) => {
  const [data, setData] = useState(initialData);
  const getData = (type, value) => {
    const newData = { ...data, [type]: value };
    setData(newData);
    setProfile("overall", newData);
  };
  return (
    <div className="space-y-10">
      <h1 className="text-2xl">Student Planner</h1>
      <div className="mt-10">
        <div className="text-justify">
          <p>
            &emsp; &emsp; Что вы делаете, когда вам нужно добраться до важного
            для вас, но незнакомого места? Вы открываете карту или включаете
            навигатор, мысленно или на листе бумаги прокладываете маршрут и
            движетесь по нему. Тогда вы точно попадете туда, куда вам надо, так
            как знаете, мимо чего пройти и где сворачивать.
          </p>
          <p>
            &emsp; &emsp; Что происходит с вами, если вы хотите чего-то достичь,
            реализовать некий замысел, но при этом не имеете понятия, что нужно
            делать каждый день, чтобы приблизиться к цели. Вы можете читать
            умные книги, визуализировать, общаться с успешными людьми, мыслить
            позитивно, но если вы не определили для себя то, что реально
            продвинет вас к цели, не видите этого и не представляете, то цели
            как были для вас недосягаемым, так им и останется.
          </p>
          <p>
            &emsp; &emsp;Постепенно вы научитесь все четче видеть те дела,
            которые реально приносят результат и продвигают вас к цели, а также
            то, что отвлекает, на что вы тратите энергию и силы впустую.
          </p>
        </div>
        <br />
        <div className="text-right">
          <i>Аляутдинов Ш. «Ежедневник. Сосредоточиться на главном».</i>
          <br />
          <b>
            Нет плохих профессий, но есть такие, которые мы уступаем другим.
          </b>
        </div>
        <br />
        <div className="text-justify">
          <p>
            &emsp; &emsp; Каждый человек в своей жизни сталкивается с проблемой
            выбора профессии. От того насколько правильно он будет сделан,
            зависит удовлетворенность трудом и качество повседневной жизни.
            Чтобы не ошибиться, ты должен сегодня осознать, что для тебя ценно,
            важно, актуально в жизни, т.е. каковы твои ценности. Ценности и
            смыслы – это та область взаимодействия личности и общества, которая
            позволяет человеку понять самого себя: «Кто я?», «Каково мое
            назначение в этой жизни?» и активно определить свое место в
            обществе. Помни, что выбор ты должен сделать сам. Это твой выбор,
            твое решение. Ты принимаешь его и несешь полную ответственность, к
            которой ты должен быть морально готов.
          </p>
          <p>
            &emsp; &emsp; Данный дневник помогут тебе, предоставит возможность
            получить знания, твоя активность, откровенные ответы при диагностике
            обследования и выполнение всех заданий.
          </p>
        </div>
      </div>
      <h1 className="text-xl font-bold text-center">Глава I. «Я и мой мир!»</h1>
      <div>
        <Hobbies getData={getData} />
        <p className="mt-5">
          *дорогой друг, просим ежегодно заполнять таблицы, даже если интересы
          не поменялись
        </p>
      </div>
      <div>
        <h1 className="text-xl font-bold mt-10 text-center">
          Глава II. «Цели на 10 лет».
        </h1>
        <p>
          Укажите масштабные, отдаленные цели, в которые сейчас, возможно, вам
          трудно поверить, но именно их вы хотели бы достичь за 10 лет. Ставьте
          цели масштабные, труднодостижимые, заряжающие на годы сосредоточенной
          работы и самоотдачи, мотивирующие к действию.{" "}
        </p>
        <p>
          Данный дневник помогут тебе, предоставит возможность получить знания,
          твоя активность, откровенные ответы при диагностике обследования и
          выполнение всех заданий.
        </p>
      </div>
      <Goals setParentData={getData} />
      <p>
        *SMART цели – это техника постановки эффективных целей в менеджменте.
        Название содержит аббревиатуру по названиям критериев, которыми обладает
        правильно поставленная цель: specific (конкретная), measurable
        (измеримая), attainable (достижимая), relevant (актуальная) и time-bound
        (ограниченная во времени).
      </p>
      <div>
        <h1 className="text-xl font-bold mt-10 text-center">
          Глава III. Активная школьная жизнь.
        </h1>
        <p>
          Напиши в каких мероприятиях в течение года Ты участвовал(а), чтобы
          познакомиться с разными профессиями? Это были экскурсии, мероприятия в
          школе, интересные поездки, встречи, беседы с родителями, друзьями,
          передачи по телевидению? (7-10)
        </p>
      </div>
      <SchoolActivity setParentData={getData} />
    </div>
  );
};

export default Profile;
