import React, { useEffect } from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { useState } from "react";

const Notes = () => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [file, setFile] = useState("documents/math.pdf");

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }
  useEffect(() => {
    setPageNumber(1);
  }, [file]);

  function nextPage() {
    if (pageNumber === numPages) {
      return pageNumber;
    }
    setPageNumber(pageNumber + 1);
    return pageNumber;
  }
  function previousPage() {
    if (pageNumber === 1) {
      return pageNumber;
    }
    setPageNumber(pageNumber - 1);
    return pageNumber;
  }

  return (
    <div className="text-center">
      <div className=" rounded-lg bg-lime-500 text-white inline-flex shadow-sm">
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 rounded-lg font-bold"
          onClick={() => {
            setFile("documents/math.pdf");
          }}
        >
          Math
        </button>
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 font-bold border-l-4"
          onClick={() => {
            setFile("documents/chemistry.pdf");
          }}
        >
          Chemistry
        </button>
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 font-bold border-l-4"
          onClick={() => {
            setFile("documents/russian.pdf");
          }}
        >
          Russian
        </button>
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 font-bold border-l-4"
          onClick={() => {
            setFile("documents/biology.pdf");
          }}
        >
          Biology
        </button>
      </div>
      <a
        className="text-center py-2 px-4 bg-lime-500 ml-5 text-white rounded-lg font-bold"
        href={file}
      >
        download PDF
      </a>
      <Document
        className="mt-10 px-auto w-3 z-10 peer"
        file={file}
        onLoadSuccess={onDocumentLoadSuccess}
      >
        <Page
          className="flex justify-center ml-[50vw]"
          pageNumber={pageNumber}
        />
      </Document>
      <div className="rounded-lg bg-lime-500 text-white inline-flex shadow-sm mt-10 absolute top-[850px] opacity-0 hover:opacity-60 peer-hover:opacity-60 left-[45%]  duration-300">
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 rounded-lg font-bold"
          onClick={() => {
            previousPage();
          }}
        >
          {"<"}
        </button>
        <div className="border py-2 px-4">
          {pageNumber} of {numPages}
        </div>
        <button
          className="py-2 px-4 hover:bg-lime-600 duration-200 rounded-lg font-bold"
          onClick={() => {
            nextPage();
          }}
        >
          {">"}
        </button>
      </div>
    </div>
  );
};

export default Notes;
