import { useContext } from "react";
import { Link } from "react-router-dom";
import { UserContext } from "../context/UserContext";
import "../index.css";

const Navbar = () => {
  const user = useContext(UserContext);
  const Buttons = () => {
    if (!user) {
      return (
        <div className="space-x-5">
          <button className="button">Sign in</button>
          <button className="button">Log in</button>
        </div>
      );
    }
    return (
      <Link to="/profile">
        <button className="button">Profile</button>
      </Link>
    );
  };

  return (
    <nav className="w-full p-1">
      <div className="self-center py-4 mx-auto max-w-[1280px] w-full font-semibold">
        <div className="flex items-center justify-between">
          <Link className="flex" to="/">
            <img className="w-[80px]" src="images/Icon.svg" alt="" />
            <div className="font-bold text-4xl ">niStudy</div>
          </Link>
          <div className="ml-auto">
            <nav className="flex space-x-10">
              <Link className="nav-item rounded-xl" to={"/intro"}>
                Introduction
              </Link>
              <Link className="nav-item rounded-xl" to={"/notes"}>
                Notes
              </Link>
              <Link className="nav-item rounded-xl" to={"/myp"}>
                MYP
              </Link>
              <Link className="nav-item rounded-xl" to={"/dp"}>
                DP
              </Link>
              {/* <Link className="nav-item rounded-xl" to="about">
                About
              </Link> */}
            </nav>
          </div>
          <div className="ml-32">
            <Buttons />
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
