import React from "react";
import { nanoid } from "nanoid";
import { useState, useRef } from "react";
import _ from "lodash";

const SchoolActivity = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      date: "",
      grade: 7,
      name: "",
      description: "",
    },
    {
      date: "",
      grade: 8,
      name: "",
      description: "",
    },
    {
      date: "",
      grade: 9,
      name: "",
      description: "",
    },
    {
      date: "",
      grade: 10,
      name: "",
      description: "",
    },
    {
      date: "",
      grade: 11,
      name: "",
      description: "",
    },
    {
      date: "",
      grade: 12,
      name: "",
      description: "",
    },
  ]);

  const inputRefs = useRef([]);

  const rows = [];

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element !== null) {
        newData[index].date = element[0].value;
        newData[index].name = element[1].value;
        newData[index].description = element[2].value;
      }
    });
    return newData;
  };

  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell">
          <input
            defaultValue={element.date}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            type="date"
            className="cell"
          ></input>
        </td>
        <td className="table-cell">{index + 7} </td>
        <td className="table-cell">
          <textarea
            defaultValue={element.name}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell"
          />
        </td>
        <td className="table-cell">
          <textarea
            defaultValue={element.description}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][2] = el;
            }}
            className="cell"
          ></textarea>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-10 p-3">Дата</td>
            <td className="table-cell w-10 p-3 ">Класс</td>
            <td className="table-cell w-96">Мероприятие, событие</td>
            <td className="table-cell w-96">
              Мои впечатления, что нового я узнал(а), увидел(а)
            </td>
          </tr>
          {rows}
        </tbody>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("schoolActivity", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default SchoolActivity;
