import React from "react";
import { useState, useRef } from "react";
import _ from "lodash";
import { nanoid } from "nanoid";

const Honors = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      year: "",
      name: "",
      results: "",
    },
  ]);
  const inputRefs = useRef([[]]);
  const removesRefs = useRef([]);

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element[0] != null) {
        newData[index].year = element[0]?.value;
        newData[index].name = element[1]?.value;
        newData[index].results = element[2]?.value;
      }
    });
    return newData;
  };

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };

  const rows = [];

  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell">
          <input
            defaultValue={element.year}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            className="cell"
          ></input>
        </td>
        <td className="table-cell">
          <textarea
            defaultValue={element.name}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell"
          ></textarea>
        </td>
        <td className="table-cell">
          <textarea
            defaultValue={element.results}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][2] = el;
            }}
            className="cell"
          />
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-30 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-44 p-3">Год</td>
            <td className="table-cell w-72 p-3">
              Наименование (предметные олимпиады, научные проекты, спартакиада,
              творческие конкурсы и тд.)
            </td>
            <td className="table-cell w-96 p-3">Результативность</td>
          </tr>
          {rows}
        </tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="3"
              onClick={() => {
                const newData = save();
                newData.push({
                  year: "",
                  name: "",
                  results: "",
                });
                setData(newData);
              }}
            >
              Add new
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("honors", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default Honors;
