import React from "react";
import SWOT from "../../components/SWOT";
import RawUniversities from "../../components/RawUniversities";
import { useState } from "react";

const Seventh = ({ initialData, setProfile }) => {
  const [data, setData] = useState(initialData);
  const getData = (type, value) => {
    const newData = { ...data, [type]: value };
    setData(newData);
    setProfile("grade7", newData);
  };
  return (
    <div className="space-y-5">
      <p className="text-xl font-bold">
        Глава IV. «Отправная точка» (1st year (7 grade).
      </p>
      <p>1. Какие профессии вам нравятся? </p>
      <SWOT
        name="Профессия №1: "
        setParentData={getData}
        dataName="profession1"
      />
      <br />
      <SWOT
        name="Профессия №2: "
        setParentData={getData}
        dataName="profession2"
      />
      <br />
      <p>2. University of my dream</p>
      <RawUniversities setParentData={getData} />
    </div>
  );
};

export default Seventh;
