import { nanoid } from "nanoid";
import React, { useEffect, useRef, useState } from "react";
import _ from "lodash";

const Hobbies = ({ initialData, getData }) => {
  const rows = [];
  const [data, setData] = useState([
    {
      year: "",
      grade: 7,
      description: "",
    },
    {
      year: "",
      grade: 8,
      description: "",
    },
    {
      year: "",
      grade: 9,
      description: "",
    },
    {
      year: "",
      grade: 10,
      description: "",
    },
    {
      year: "",
      grade: 11,
      description: "",
    },
    {
      year: "",
      grade: 12,
      description: "",
    },
  ]);

  useEffect(() => {});

  const inputRefs = useRef([[]]);

  const getValues = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      newData[index].year = element[0].value;
      newData[index].description = element[1].value;
    });
    setData(newData);
    getData("hobbies", newData);
  };
  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell">
          <input
            autoComplete="off"
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            defaultValue={element.year}
            className="cell"
          ></input>
        </td>
        <td className="table-cell">{element.grade} </td>
        <td className="table-cell">
          <textarea
            defaultValue={element.description}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell"
          ></textarea>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <th className="table-cell w-10 p-3">Учебный год</th>
            <th className="table-cell w-10 p-3 ">Класс</th>
            <th className="table-cell w-96 ">Я увлекаюсь...</th>
          </tr>
          {rows}
        </tbody>
      </table>
      <button
        className="button"
        onClick={() => {
          getValues();
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default Hobbies;
