import React from "react";
import { FiYoutube } from "react-icons/fi";

const Main = () => {
  return (
    <div className="mx-36">
      <div className="h-[65vh] flex space-x-[150px] mt-16">
        <div className="flex flex-col space-y-4 w-[200vw] ">
          <h1 className="text-5xl font-extrabold">NISTUDY</h1>
          <h1 className="text-xl font-thin">
            Welcome to NISTUDY, everything you should know about the Nazarbayev
            Intellectual School of International Baccalaureate. There are a lot
            of functions the project could deliver however, its main aim is to
            introduce the NISA IB inside and out along with helping both
            applicants and current students of the school by introducing the
            school from different angles in forms of visual, audial, and textual
            information. The range of the information gathered in NISTUDY is
            outstanding because, with the help of the project, the possibility
            of getting acquainted with every aspect of the school starting from
            the entrance examination, including the school’s features, and
            finishing with the final exam is presented. Thus, there is no
            difference in what sort of information has the highest demand. The
            project was able to present its content today with the help of NISA
            DP’23 students the cooperated together to achieve the common goal of
            helping the school’s community and being able to show creativity.
          </h1>
          <div className="block">
            {/* <button className="font-bold py-2  px-4 rounded-lg bg-lime-500 flex hover:bg-lime-600 text-white">
              <FiYoutube size={25} className="w-20"></FiYoutube>
            </button> */}
          </div>
        </div>
        <div>
          <img className="w-[3000px]" src="images/landing.png" alt="" />
        </div>
      </div>
      <div className="h-40" />
    </div>
  );
};

export default Main;
