import { nanoid } from "nanoid";
import React from "react";
import { useState, useRef } from "react";
import _ from "lodash";

const SAT = ({ initialData, setParentData }) => {
  const [data, setData] = useState([
    {
      date: "",
      math: "",
      english: "",
      overall: "",
    },
  ]);

  const inputRefs = useRef([[]]);
  const removesRefs = useRef([]);

  const save = () => {
    const newData = _.cloneDeep(data);
    inputRefs.current.forEach((element, index) => {
      if (element[0] != null) {
        newData[index].date = element[0]?.value;
        newData[index].math = element[1]?.value;
        newData[index].english = element[2]?.value;
        newData[index].overall = element[3]?.value;
      }
    });
    return newData;
  };

  const removeItem = (index) => {
    const newData = save();
    newData.splice(index, 1);
    inputRefs.current = [];

    setData(newData);
  };

  const rows = [];
  data.forEach((element, index) => {
    rows.push(
      <tr key={nanoid()}>
        <td className="table-cell ">
          <input
            defaultValue={element.date}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][0] = el;
            }}
            type="date"
            className="cell w-40"
          ></input>
        </td>
        <td className="table-cell ">
          <input
            type="number"
            defaultValue={element.math}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][1] = el;
            }}
            className="cell w-40"
          ></input>
        </td>
        <td className="table-cell ">
          <input
            type="number"
            defaultValue={element.english}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][2] = el;
            }}
            className="cell w-40"
          ></input>
        </td>
        <td className="table-cell ">
          <input
            type="number"
            defaultValue={element.overall}
            ref={(el) => {
              inputRefs.current[index] = inputRefs.current[index] || [];
              inputRefs.current[index][3] = el;
            }}
            className="cell w-40"
          ></input>
        </td>
        <td>
          <div
            ref={(el) => (removesRefs.current[index] = el)}
            onClick={() => {
              removeItem(index);
            }}
            className="group"
          >
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-10 duration-300 group-hover:rotate-45"></div>
            <div className="absolute w-6 h-2 bg-red-600 group-hover:opacity-100 opacity-10 duration-300 group-hover:rotate-[-45deg]"></div>
          </div>
        </td>
      </tr>
    );
  });
  return (
    <div className="field">
      <table className="table-auto mx-auto mt-5">
        <tbody>
          <tr>
            <td className="table-cell w-40 ">Date</td>
            <td className="table-cell w-40 ">Math</td>
            <td className="table-cell w-40 ">English</td>
            <td className="table-cell w-40 ">Overall</td>
          </tr>
          {rows}
        </tbody>
        <tfoot>
          <tr>
            <th
              className="table-cell"
              colSpan="5"
              onClick={() => {
                const newData = save();
                newData.push({
                  date: "",
                  math: "",
                  english: "",
                  overall: "",
                });
                setData(newData);
              }}
            >
              Add new
            </th>
          </tr>
        </tfoot>
      </table>
      <button
        className="button"
        onClick={() => {
          const newData = save();
          setData(newData);
          setParentData("SAT", newData);
        }}
      >
        Save changes
      </button>
    </div>
  );
};

export default SAT;
